.Home .GridStyle {
  width: 100%;
}
.Home .GridStyle ul {
  width: 100%;
}
.Home .GridStyle ul li {
  height: 80px;
  width: 25%;
  float: left;
  text-align: center;
  padding: 10px;
  box-sizing: border-box;
}
.Home .GridStyle ul li div {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background: #7fdbff;
  display: inline-block;
  text-align: center;
  line-height: 40px;
  color: #fff;
}
.Home .GridStyle ul li:nth-of-type(2) div {
  background: #ff4136;
}
.Home .GridStyle ul li:nth-of-type(3) div {
  background: #f012be;
}
.Home .GridStyle ul li:nth-of-type(4) div {
  background: #3d9970;
}
.Home .GridStyle ul li:nth-of-type(5) div {
  background: #0074d9;
}
.Home .GridStyle ul li:nth-of-type(6) div {
  background: #ff851b;
}
.Home .GridStyle ul li:nth-of-type(7) div {
  background: #85144b;
}
.Home .GridStyle ul li:nth-of-type(8) div {
  background: #aaa;
}
.Home .Recommend {
  width: 100%;
  padding: 0 10px;
  box-sizing: border-box;
}
.Home .Recommend > p {
  line-height: 28px;
  font-size: 14px;
  font-weight: 700;
}
.Home .Recommend ul li {
  float: left;
  width: 46%;
  margin: 2%;
  height: 90px;
  background: #0074d9;
  box-shadow: 0 4px 16px 0 #0074d9;
  padding: 6px 12px;
  box-sizing: border-box;
  color: #fff;
  font-size: 13px;
  border-radius: 6px;
}
.Home .Recommend ul li p:nth-of-type(1) {
  margin-bottom: 12px;
}
.Home .Recommend ul li p:nth-of-type(2) {
  line-height: 22px;
  text-indent: 2em;
}
.Home .Article {
  width: 100%;
  padding: 0 10px;
  box-sizing: border-box;
  margin-top: 10px;
}
.Home .Article > p {
  font-weight: 700;
}
.Home .Article ul li {
  width: 100%;
  height: 100px;
  margin: 10px 0;
  background-position: 50% 50%;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 4px;
}
.Home .Article ul li div {
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  text-align: center;
  line-height: 100px;
  padding: 0 6px;
  box-sizing: border-box;
  position: relative;
}
.Home .Article ul li div p {
  color: #fff;
  font-size: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.Home .Article ul li div span {
  height: 20px;
  line-height: 20px;
  position: absolute;
  bottom: 6px;
  right: 6px;
  padding: 1px 4px;
  background: rgba(0, 0, 0, 0.4);
  text-align: center;
  color: #fff;
  border-radius: 4px;
  font-size: 12px;
}
.Home .category {
  width: 100%;
  padding: 0 10px;
  box-sizing: border-box;
}
.Home .category > p {
  font-weight: 700;
  margin-top: 6px;
  line-height: 28px;
}
.Home .category ul li {
  float: left;
  width: 32.1%;
  margin: 0.6%;
  height: 100px;
  text-align: center;
  color: #fff;
  padding: 20px 0;
  box-sizing: border-box;
}
.Home .category ul li:nth-of-type(1),
.Home .category ul li:nth-of-type(3),
.Home .category ul li:nth-of-type(8) {
  background: rgba(220, 129, 55, 0.8);
}
.Home .category ul li:nth-of-type(2),
.Home .category ul li:nth-of-type(4),
.Home .category ul li:nth-of-type(6) {
  background: rgba(38, 191, 142, 0.8);
}
.Home .category ul li:nth-of-type(5),
.Home .category ul li:nth-of-type(7) {
  background: rgba(72, 137, 199, 0.8);
}
.Home .category ul li:nth-of-type(7) {
  width: 65.3%;
}
.Home .category ul p {
  line-height: 30px;
}

